window.htmlfont = '';
window.isMobile = false;
!(function(window) {
    const n = document.documentElement;
    let rootfont;
    const i = document.createElement('style');
    n.firstElementChild.appendChild(i);

    function infinite() {
        // var docW = document.documentElement.clientWidth;
        let docW = window.innerWidth;

        if (navigator.userAgent.match(/Android|BlackBerry|iPhone|iPad|iPod|Opera Mini|IEMobile/i)) {
            isMobile = true;
        }
        if (docW < 320) {
            docW = 320;
            rootfont = (100 / 750) * docW;
            i.innerHTML = 'html{font-size:' + rootfont + 'px!important;visibility: visible;}';
        } else if (docW <= 750) {
            rootfont = (100 / 750) * docW;
            i.innerHTML = 'html{font-size:' + rootfont + 'px!important;visibility: visible;}';
        } else {
            i.innerHTML = 'html{visibility: visible;}';
        }
        htmlfont = rootfont;
    }
    window.addEventListener(
        'resize',
        function() {
            infinite();
        },
        !1,
    );

    window.addEventListener(
        'pageshow',
        function(e) {
            // pageshowload
            // eventpersistedtrue
            // JSload
            // 
            e.persisted && infinite();
        },
        !1,
    ),
        infinite();
})(window);
