       //SELECT video_frame_rate, audio_frame_rate, video_bit_rate, audio_bit_rate, timestamp, stream_id_push FROM StreamQuality WHERE stream_id_push > 0 and user_id == '${userID}' and room_id == '${roomID}'
       
